$grey: #f6f6f6;
$orange: #ea744a;
$blue: #4eacf8;

.detail-menu-modal {
  p {
    font-size: 1.1rem;
  }
  background-color: white;
  width: 40%;
  min-width: 300px;
  max-width: 600px;
  max-height: 100%;

  overflow-y: scroll;

  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: 1011;

  .detail-menu-modal-img {
    width: 100%;
    height: auto;
    padding-bottom: 10px;
  }

  .detail-modal-content {
    padding: 30px;
    .detail-modal-info {
      border-bottom: 1px solid $grey;
      padding: 10px 0;
      p {
        color: #a4adb8;
      }
    }
    .detail-modal-price {
      padding: 10px 0;
      display: flex;
      justify-content: space-between;
      p {
        font-weight: bold;
      }
    }

    .detail-modal-count {
      padding: 10px 0;

      display: flex;
      justify-content: space-between;
      align-items: center;
      p {
        font-weight: bold;
      }
    }

    .detail-modal-count-btn {
      display: flex;
      align-items: center;
      p {
        padding: 0 10px;
      }
      button {
        width: 30px;
        height: 30px;
        background-color: white;
        border-radius: 100%;
        border: 0.5px solid #939daa;
      }
    }

    .detail-modal-select-menus {
      .menus-group {
        .menus-group-name {
          background-color: $grey;
          padding: 10px;
          display: flex;
          justify-content: space-between;
          p:nth-child(1) {
            font-weight: bold;
            font-size: 1rem;
          }
          p:nth-child(2) {
            color: $orange;
            font-size: 1rem;
          }
        }
        .menus-group-menu {
          display: flex;
          justify-content: space-between;
          padding: 10px;
          input {
            margin-right: 5px;
          }
        }
      }
    }
  }

  .detail-model-submit {
    position: sticky;
    bottom: 0;
    button {
      background-color: $blue;
      width: 100%;
      height: 50px;
      color: white;
    }
  }
}

.detail-menu-modal::-webkit-scrollbar {
  display: none;
}

@media screen and (max-width: 768px) {
  .detail-menu-modal {
    background-color: white;
    width: 100%;
    max-width: 768px;
    height: 100%;

    .detail-model-submit {
      position: sticky;
      bottom: 0;
      button {
        background-color: $blue;
        width: 100%;
        height: 50px;
        color: white;
      }
    }
  }
}
